@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input, button, submit { 
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
} 

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'BetonSans';
  src: local('BetonSans'), url(/marvel-app/static/media/BentonSans-Comp-Black-Regular.697ae783.otf) format('truetype');
}

html {
  scroll-behavior: smooth;
}

.comicsNotFoundContainer{height:500px;text-align:center;display:flex;justify-content:center;align-items:center}

.container{display:flex;flex-direction:column}.title{color:black;font-family:BetonSans, sans-serif;font-size:calc(15px + 2vmin);text-decoration:none;text-transform:uppercase}

.shadowFilter{height:100%;width:100%;position:absolute;background-color:rgba(28,28,29,0.671);top:0;left:0}.backgroundImg{overflow:hidden;height:100vh;width:100vw;background-repeat:no-repeat;background-position:top;background-size:cover;-webkit-filter:blur(5px);filter:blur(5px);transform:scale(1.1)}.backgroundImgContainer{overflow:hidden;height:100vh;width:100%;top:0;left:0;position:absolute;z-index:10}.welcomeSection{background-color:transparent;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(15px + 2vmin);font-weight:bold;color:white;position:relative;z-index:100}.welcomeSectionContainer{display:flex;align-items:center;width:100%;flex-direction:row;justify-content:space-evenly}.welcomeSectionContainer-tablet{display:flex;align-items:center;flex-direction:column;justify-content:center;width:75vmin}.welcomeSectionContainer-mobile{width:100%;display:flex;align-items:center;flex-direction:column;justify-content:center}.welcomeSectionTxt{align-self:center;max-width:512px}.welcomeSectionTxtAppear{animation:appear 1.5s ease-out both;-webkit-animation:appear 1.5s ease-out both}@keyframes appear{0%{-webkit-opacity:0;opacity:0}100%{-webkit-opacity:1;opacity:1}}.welcomeSectionTxt-tablet{margin-top:64px;align-self:center}.welcomeSectionTxt-mobile{margin-top:64px;align-self:center}.animatedImg{pointer-events:none;width:60vmin}.animatedImg-tablet{max-height:500px}.animatedImg-mobile{max-height:320px}.animatedImgSlideRight{animation:slideRight 1s ease-out both;-webkit-animation:slideRight 1s ease-out both}@keyframes slideRight{0%{transform:translateX(-100px);-webkit-opacity:0;opacity:0}100%{transform:translateX(0);-webkit-opacity:1;opacity:1}}

.buttonContainer{color:white;background-color:#F0141E;height:32px;width:100%}.buttonContainer:hover{background-color:#c8131c}

.carouselItem{width:224px;height:90%;display:flex;flex-direction:column;background-color:white;border-radius:4px}.carouselItem:hover{box-shadow:-1px 8px 30px 4px rgba(0,0,0,0.24);-webkit-box-shadow:-1px 8px 30px 4px rgba(0,0,0,0.24);-moz-box-shadow:-1px 8px 30px 4px rgba(0,0,0,0.24);color:#F0141E}.carouselItem-tablet{width:300px;height:90%}.carouselItemContainer{height:500px;width:400px;margin-left:auto;margin-right:auto;display:flex;flex-direction:column;justify-content:center;align-items:center}.carouselItemContainer-mobile{width:100%}.carouselImage{display:flex;height:100%;width:100%;margin-left:auto;margin-right:auto}.carouselImageContainer{display:flex;overflow:hidden;height:70%;width:100%;margin-left:auto;margin-right:auto;align-items:center;justify-content:center}.carouselImageContainer:hover .carouselImage{-webkit-filter:blur(5px);filter:blur(5px);transform:scale(1.1)}.carouselImageContainer:hover #seeMore{opacity:1}.carouselImageContainer:hover .numberOfComicIndicator{opacity:0}#seeMore{opacity:0;transition:all 0.5s;z-index:100;align-self:center;position:fixed;color:#F0141E;font-family:BetonSans, sans-serif;font-size:32px;text-decoration:none;text-transform:uppercase}.numberOfComicIndicator{border-radius:50%;width:36px;height:36px;background-color:#F0141E;color:white;text-align:center;transition:all 0.5s;z-index:100;align-self:flex-start;position:fixed;font-family:BetonSans, sans-serif;font-size:32px;text-decoration:none}.itemFooter{display:flex;flex:1 1;flex-direction:column;justify-content:space-between}#arrow{position:relative;top:0;transition:top ease 0.5s}#arrow:hover{top:-10px}.comicTitle{overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical}.loading{position:relative;background-color:#e2e2e2}.loading::after{display:block;content:"";position:absolute;width:100%;height:100%;transform:translateX(-100%);background:linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);animation:loading 3s infinite}@keyframes loading{100%{transform:translateX(100%)}}

.searchBar{width:40%;height:32px;background:white;border-radius:4px;font-size:17px;display:flex;flex-direction:row}.searchBar-mobile{width:100%}

.cardContainer{border-radius:4px;padding:20px;max-width:600px;width:100%}

.formInput{width:100%;padding:12px;border:1px solid #ccc;border-radius:4px;box-sizing:border-box;margin-top:6px;margin-bottom:16px;resize:vertical}.errorBorder{border:1px solid #F0141E}.errorLabel{color:#F0141E}

.navBar{height:64px;position:fixed;top:0;left:0;width:100%;z-index:100000;background-color:#202020;display:flex;flex-direction:row;align-items:center}.navBar-mobile{height:48px;align-items:flex-start;justify-content:space-between}.marvelLogo{height:100%;background-color:#F0141E}.marvelLogo-mobile{display:flex;justify-content:center;align-items:center}.burgerButton{display:block;align-self:center;cursor:pointer}.burgerButtonContainer{display:flex;height:100%;width:36px;align-items:center;justify-content:center}.burgerButtonContainer-opened{height:48px;display:flex;justify-content:flex-end;align-items:flex-end;width:100%}.sideBarMenu{background-color:#353434;justify-content:flex-start;width:128px;height:100vh;display:flex;flex-direction:column}.menuItem{display:inline-block;padding-right:16px;padding-left:16px}.menuItem::after{content:'';width:0px;height:2px;display:block;background:#fff;transition:300ms}.menuItem:hover{color:#F0141E}.menuItem:hover::after{width:100%}.marvelDefaultText{color:white;font-family:BetonSans, sans-serif;font-size:32px;text-decoration:none;align-self:center;text-transform:uppercase}

